export default {
    name: 'Map',

    data() {
        return {
            contact: []
        }
    },
    async fetch() {
        const { json: data } = await this.$kirby.find({
            "query": "page('home')",
            "select": {
                "contactmail": true,
                "contacttel": true,
                "contactadd": true,
                "mapframe": true,
            }
        }, this.$nuxt.context.app.i18n.locale)
        this.contact = data
    }
}
